* {
    padding: 0;
    margin: 0;
    border: none;
}

*, *::before, *::after {
    box-sizing: border-box;
}

html, body {
    width: 100vw;
    height: 100vh;
    overflow: hidden; /* Полный запрет прокрутки */
}

body {
    background-color: rgb(59, 58, 58);
    display: flex;
    align-items: center;
    justify-content: center;
}

#game-container {
    width: 318px;
    height: 500px;
}